import { hyperLinkTargetProvider } from './links/target/target';
export function openUrl(options: {
    target: string;
    url: string;
    width?: number;
    height?: number;
    attr?: {
        center?: boolean;
        top?: number;
        left?: number;
    },
    byPost?: boolean
    parameter?: string;
    title?: string;
}) {
    const { target, url, ...op } = options;

    return hyperLinkTargetProvider.handle(target, url, op);
}
